.math-input-buttons.absolute {
  left: -2px;
  position: absolute;
  top: -3px;
  z-index: 5;
}
.tex-button {
  display: block;
  float: left;
  width: 35px;
  height: 35px;
  margin: 2px;
  border: 1px solid #1c758a;
  background-color: white;
  border-radius: 5px;
}
.tex-button:hover {
  cursor: pointer;
  background-color: #f0f0f0;
}
.tex-button:focus {
  border: 2px solid #1c758a;
  outline: none;
}
.tex-button-row {
  margin: 5px 0;
}
.tex-button-row:first-child {
  margin-top: 0;
}
.tex-button-row:last-child {
  margin-bottom: 0;
}
/*
end of styles copied form perseus
