.fm-button-light {
  border: 2px solid transparent;
  display: inline-block;
  margin: 2px;
  padding: 0px 8px 0px 8px;
  background-color: white;
  font-size: 12px;
  font-weight: 600;
  color: #212121;
  border-radius: 15px;
}

.fm-button-light:hover {
  cursor: pointer;
  background-color: #f2f2f2;
}
.fm-button-light:focus {
  border: 2px solid #ffa535;
  outline: none;
}

.fm-button {
  border: 2px solid transparent;
  padding: 4px 8px;
  margin: 4px;
  background-color: #0d57bf;
  color: #f1f1f1;
  border-radius: 15px;
  font-size: 12px;
  font-weight: 600;
}

.fm-button:hover
{
  cursor: pointer;
  background-color: #052d66;
}

.fm-button:focus {
  border: 2px solid #ffa535;
  outline:0;
}

.fm-button:active {
  -webkit-transform: scale(0.99);
      -ms-transform: scale(0.99);
          transform: scale(0.99);
}


.fm-button-left {
  border: 2px solid transparent;
  padding: 4px 4px 4px 8px;
  margin-left: 4px;
  margin-right: 0px;
  background-color: #0d57bf;
  color: #f1f1f1;
  border-top-left-radius: 15px;
  border-bottom-left-radius: 15px;
  border-top-right-radius: 0px;
  border-bottom-right-radius: 0px;
  font-size: 12px;
  font-weight: 600;
}

.fm-button-left:focus {
  border-left: 2px solid #ffa535;
  border-top: 2px solid #ffa535;
  border-top: 2px solid #ffa535;
  outline:0;
}

.fm-button-right {
  border: 2px solid transparent;
  padding: 4px 8px 4px 4px;
  margin-right: 4px;
  margin-left: 0px;
  background-color: #0d57bf;
  color: #f1f1f1;
  border-top-right-radius: 15px;
  border-bottom-right-radius: 15px;
  border-top-left-radius: 0px;
  border-bottom-left-radius: 0px;
  font-size: 12px;
  font-weight: 600;
}

.fm-button-left:focus {
  border-right: 2px solid #ffa535;
  border-top: 2px solid #ffa535;
  border-top: 2px solid #ffa535;
  outline:0;
}

.fm-button-selected {
  background-color: #052d66
}

.fm-button-selected:hover {
  background-color: #011024
}

.fm-close-button {
  border: 2px solid transparent;
  float: right;
  display: inline-block;
  height: 20px;
  margin: 2px;
  padding: 4px 6px 18px 6px;
  background-color: white;
  border: 1px solid #d1d1d1;
