html {
  height: 100%;
}
body {
  min-height: 100%;
  font-family: 'Roboto',sans-serif;
  color: #222;
  /* background-color: #e7effd; */
  /* background-color: #92d1ff; */
  margin: 0;
  padding: 0;
  line-height: 1.5
}
#body {
  padding: 1em;
}

p {
  font-size: 18px;
}

code {
  font: 90% monospace;
  background: #DDD;
  border-radius: .3em;
  -moz-border-radius: .3em;
  -webkit-border-radius: .3em;
  padding: 0 .3em;
}
code:hover {
  color: #DDD;
  background: #222;
}
a {
  color: mediumblue;
  text-decoration: none;
}

a:hover {
  color: black;
  color: orange;
}
a:active {
  color: #400;
}

a.lightLink {
  color: white;
  text-decoration: none;
}

a.lightLink:hover {
  color: black;
  color: orange;
}

a.lightLink:active {
  color: #400;
}
ul {
  padding-left: 1em;
}

